import { InjectionToken, FactoryProvider } from "@angular/core";

export const DOCUMENT_TOKEN = new InjectionToken("Document");

export function provideDocumentFactory() {
    return (typeof window !== "undefined") ? window.document : null;
}

// TODO: should get document from window provider
// http://stackoverflow.com/a/37176929
export const DOCUMENT_PROVIDER: FactoryProvider = {
    provide: DOCUMENT_TOKEN,
    useFactory: provideDocumentFactory
};
