import { Component, Input, Output, EventEmitter, OnInit, OnChanges } from "@angular/core";

const buttonClass = "sf-button";
const buttonIconClass = "sf-button__icon sf-icon sf-fa sf-fa-";
const buttonFeedbackIcon = "check";

@Component({
    selector: "sf-button",
    templateUrl: "./button.component.html"
})
/**
 * Sets the look of the button, empty by default
 *
 * Can be large, small, action, delete, feedback, or any other string
 *
 * Can have more that one look separated by space. For example,  look="action large"
 */
export class SfButtonComponent implements OnInit, OnChanges {
    @Input() look: string;
    /**
     * Sets the type of a button, button by default
     *
     * Can be button, submit or reset
     */
    @Input() type: string = "button";
    @Input() icon: string;
    @Input() iconLook: string = "size-xs";
    @Input() iconTitle: string;
    /**
     * Use to make loader in the button. Empty by default.
     *
     * To add loader to the button set loader="color" for default buttons and loader="transparent" for action and delete buttons
     */
    @Input() loader: string;
    @Input() feedback: boolean;
    @Input() dropdown: boolean = false;
    @Input() dropped: boolean = false;
    @Input() disabled: any;
    @Input() title: string;
    @Input() ariaLabel: string;
    @Output() onClick: EventEmitter<any> = new EventEmitter<any>();
    @Output() onMouseEnter: EventEmitter<any> = new EventEmitter<any>();
    @Output() onMouseDown: EventEmitter<any> = new EventEmitter<any>();

    buttonClassName: string;
    iconClassName: string;

    private contentWrapperEmpty: boolean = false;

    ngOnInit() {
        this.generateCssClasses();
    }

    ngOnChanges(changes: any) {
        this.generateCssClasses();
    }

    handleMouseEnter() {
        this.onMouseEnter.emit();
    }

    handleMouseDown() {
        this.onMouseDown.emit();
    }

    handleClick(event: any) {
        if (this.disabled) {
            event.stopImmediatePropagation();
            return;
        }

        if (!this.dropdown) {
            this.onClick.emit(event);
        }
    }

    toggleIsDropped() {
        this.dropped = !this.dropped;
        this.onClick.emit(this.dropped);
    }

    private generateCssClassList() {
        let cls = buttonClass;
        if (this.look) {
            cls += " -" + this.look.split(" ").join(" -");
        }
        if (this.icon && !this.feedback) {
            cls += " -icon";
        }
        if (this.loader) {
            cls += " -loader -inprogress";
        }
        if (this.feedback) {
            cls += " -feedback -done";
        }
        if (this.dropped) {
            cls += " -dropdown -dropped";
        }
        if (this.contentWrapperEmpty) {
            cls += " -icon-only";
        }
        return cls;
    }

    private generateIconCssClassList() {
        let cls = buttonIconClass;
        if (!this.feedback) {
            cls += this.icon;
        } else {
            cls += buttonFeedbackIcon;
        }
        if (this.iconLook) {
            cls += " -" + this.iconLook.split(" ").join(" -");
        }
        return cls;
    }

    private generateCssClasses() {
        this.buttonClassName = this.generateCssClassList();
        this.iconClassName = this.generateIconCssClassList();
    }
}
