import { Component, Input, OnInit, OnChanges } from "@angular/core";

const iconClass = "sf-icon";

@Component({
    selector: "sf-icon",
    templateUrl: "./icon.component.html"
})

export class SfIconComponent implements OnInit, OnChanges {
    @Input() name: string;
    @Input() look: string;
    iconCssClassList: string;

    ngOnInit() {
        this.iconCssClassList = this.generateCssClassList();
    }

    ngOnChanges(changes: any) {
        this.iconCssClassList = this.generateCssClassList();
    }

    generateCssClassList() {
        let cls = iconClass;
        if (this.name) {
            cls += " sf-fa sf-fa-" + this.name;
        } else {
            cls += " -sf-inline-block";
        }
        if (this.look) {
            cls += " -" + this.look.split(" ").join(" -");
        }
        return cls;
    }
}
