import {  InjectionToken, FactoryProvider } from "@angular/core";

// TODO: #227311 move to shared
export const SF_WINDOW_TOKEN = new InjectionToken("Window");

export function sfGetWindowFactory() {
    return (typeof window !== "undefined") ? window : null;
}
// http://stackoverflow.com/a/37176929
export const SF_WINDOW_PROVIDER: FactoryProvider = {
    provide: SF_WINDOW_TOKEN,
    useFactory: sfGetWindowFactory
};
