import {
    Directive,
    EventEmitter,
    Output,
    Input,
    ElementRef,
    Inject,
    OnDestroy,
    OnInit
} from "@angular/core";
import { BehaviorSubject } from "rxjs";
import { SfRxBaseComponent } from "../../utilities/rx-base.component";
import { SF_WINDOW_TOKEN } from "../../utilities/window.provider";

const ENTER_KEY_CODE = 13;
const ESCAPE_KEY_CODE = 27;
const TAB_KEY_CODE = 9;

/** @dynamic */
@Directive({ selector: "[sfTrackFocus]" })
export class SfTrackFocusDirective extends SfRxBaseComponent implements OnDestroy, OnInit {
    @Output() contentFocusOut = new EventEmitter();
    @Output() contentFocusIn = new EventEmitter();
    @Input() closeOnContentClick = true;
    @Input() closeOnEnterKey = true;
    @Input() closeOnEscape = true;
    @Input() focusSubscription = new BehaviorSubject<boolean>(false);
    @Input() windowElement: Window;

    private mouseDownEventListener: EventListener;
    private mouseUpEventListener: EventListener;
    private clickEventListener: EventListener;
    private documentClickEventListener: EventListener;
    private keydownEventListener: EventListener;

    private isItemFocused = false;
    private hostEvent = null;
    private currentWindow: Window;

    private elMouseUpTriggered = false;
    private elMouseDownTriggered = false;

    constructor(private el: ElementRef, @Inject(SF_WINDOW_TOKEN) private window: Window) {
        super();
        this.mouseUpEventListener = evt => this.onElementMouseUp(evt);
        this.mouseDownEventListener = evt => this.onElementMouseDown(evt);
        this.clickEventListener = evt => this.onElementClick(evt);
        this.documentClickEventListener = evt => this.onDocumentClick(evt);
        this.keydownEventListener = evt => this.onDocumentEnterKey(evt);
        this.el.nativeElement.addEventListener("mouseup", this.mouseUpEventListener);
        this.el.nativeElement.addEventListener("mousedown", this.mouseDownEventListener);
        this.el.nativeElement.addEventListener("click", this.clickEventListener);
        this.currentWindow = this.window;
    }

    ngOnInit() {
        // Setting different window element. For example when used in iframe.
        if (this.windowElement) {
            this.currentWindow = this.windowElement;
        }

        this.registerSubscription(
            this.focusSubscription.subscribe(event => {
                this.isItemFocused = event;
                if (event) {
                    this.addEventListeners();
                } else {
                    this.removeEventListeners();
                }
            })
        );
    }

    ngOnDestroy() {
        super.ngOnDestroy();
        this.el.nativeElement.removeEventListener("click", this.clickEventListener);
        this.el.nativeElement.removeEventListener("mousedown", this.mouseDownEventListener);
        this.el.nativeElement.removeEventListener("mouseup", this.mouseUpEventListener);
        this.removeEventListeners();
    }

    onElementMouseUp(event: Event) {
        this.elMouseUpTriggered = true;
    }

    onElementMouseDown(event: Event) {
        this.elMouseDownTriggered = true;
    }

    onElementClick(event: Event) {
        this.hostEvent = event;
        if (this.isItemFocused === false) {
            this.isItemFocused = true;
            this.contentFocusIn.emit(event);

            this.addEventListeners();
        } else if (this.isItemFocused && this.closeOnContentClick) {
            this.callFocusOut(event);
        }
        this.resetElementClickFlags();
    }

    onDocumentClick(event: Event) {
        if (this.elMouseDownTriggered && !this.elMouseUpTriggered) {
            // this means that the mouse click started in the field and left while dragging/selecting and the mouse
            // was released somewhere outside of the field, in which case we don't want to focus out the element
            this.resetElementClickFlags();
            return;
        }
        if (this.hostEvent !== event) {
            this.callFocusOut(event);
        }
        this.resetElementClickFlags();
    }

    onDocumentEnterKey(event: Event) {
        const keyboardEvent = event as KeyboardEvent;
        if (this.isItemFocused) {
            if (keyboardEvent.keyCode === ENTER_KEY_CODE && this.closeOnEnterKey) {
                this.callFocusOut(event);
            } else if (keyboardEvent.keyCode === ESCAPE_KEY_CODE && this.closeOnEscape) {
                this.callFocusOut(event);
            } else if (keyboardEvent.keyCode === TAB_KEY_CODE || keyboardEvent.shiftKey && keyboardEvent.keyCode === TAB_KEY_CODE) {
                this.callFocusOut(event);
            }
        }
    }

    private callFocusOut(event: Event) {
        this.hostEvent = null;
        this.isItemFocused = false;

        this.contentFocusOut.emit(event);
        this.removeEventListeners();
    }

    private addEventListeners() {
        this.currentWindow.document.addEventListener("click", this.documentClickEventListener);
        this.currentWindow.addEventListener("keydown", this.keydownEventListener);
    }

    private removeEventListeners() {
        this.currentWindow.document.removeEventListener("click", this.documentClickEventListener);
        this.currentWindow.removeEventListener("keydown", this.keydownEventListener);
    }

    private resetElementClickFlags() {
        this.elMouseDownTriggered = false;
        this.elMouseUpTriggered = false;
    }
}
