import { Component, OnInit, Input, TemplateRef, ElementRef, ViewChild } from "@angular/core";

export enum ChartLoaderTypes {
    Donut = "donut",
    Line = "line",
    HeatMap = "heat-map"
}

@Component({
    selector: "sf-chart-loader",
    templateUrl: "./chart-loader.component.html",
    styleUrls: ["./chart-loader.component.scss"]
})
export class SfChartLoaderComponent implements OnInit {
    @Input() type: ChartLoaderTypes | string;
    @Input() look: string;

    @ViewChild("donut", {static: true}) private donut: TemplateRef<ElementRef>;
    @ViewChild("heatMap", {static: true}) private heatMap: TemplateRef<ElementRef>;
    @ViewChild("lineChart", {static: true}) private lineChart: TemplateRef<ElementRef>;

    loaderTemplate: TemplateRef<ElementRef>;
    loaderCssClass = "sf-chart-loader";

    ngOnInit() {
        switch (this.type) {
            case ChartLoaderTypes.Donut:
                this.loaderTemplate = this.donut;
                break;
            case ChartLoaderTypes.HeatMap:
                this.loaderTemplate = this.heatMap;
                break;
            case ChartLoaderTypes.Line:
                this.loaderTemplate = this.lineChart;
                break;
        }

        this.generateCssClassList();
    }

    private generateCssClassList() {
        if (this.look) {
            this.loaderCssClass += " -" + this.look.split(" ").join(" -");
        }

        if (this.type) {
            this.loaderCssClass += ` -${this.type}`;
        }
    }
}
