import { createCustomElement } from "@angular/elements";
import { Type, Injector, ComponentFactoryResolver, Injectable } from "@angular/core";

@Injectable()
export class SfElementFactory {
    constructor(private injector: Injector, private resolver: ComponentFactoryResolver) {
    }

    createCustomElementDefinition(component: Type<any>, name: string = null) {
        let elementName = name;
        if (!elementName) {
            const factory = this.resolver.resolveComponentFactory(component);

            // Suffix is added in order to avoid name collisions with component selectors
            elementName = `${factory.selector}-element`;
        }

        const definition = customElements.get(elementName);
        if (!definition) {
            const customHtmlElement = createCustomElement(component, { injector: this.injector });
            if (customHtmlElement instanceof Function) {
                customElements.define(elementName, customHtmlElement);
            }
        }
    }
}
