import { Component, Input, OnInit } from "@angular/core";

const singleRowHeight = 16;
const loaderClass = "sf-loader";
const firstRectClass = "-first";
const secondRectClass = "-second";
const thirdRectClass = "-third";

@Component({
    selector: "sf-loader",
    templateUrl: "./loader.component.html",
    styleUrls: ["./loader.component.scss"]
})
export class SfLoaderComponent implements OnInit {
    @Input() look: string;
    @Input() count: number;
    @Input() height: number;

    iterations: number[] = [0];
    rectClasses: Array<string>;

    ngOnInit() {
        if (this.count) {
            this.iterations = Array(this.count).fill(0);
        }

        this.rectClasses = [firstRectClass, secondRectClass, thirdRectClass];

        if (this.look) {
            if (this.look.includes("progressbar") || this.look.includes("skeleton")) {
                this.rectClasses.length = 0;
            }
        }

        if (this.height) {
            this.iterations.length = 1;
            this.rectClasses.length = Math.floor(this.height / singleRowHeight);
        }
    }

    generateCssClassList() {
        let cls = loaderClass;

        if (this.look) {
            cls += " -" + this.look.split(" ").join(" -");
        }

        return cls;
    }
}
