import { Component, Input, ViewChild, OnInit } from "@angular/core";

const iconClass = "sf-badge";
const SYMBOLS_COUNT_TO_TRIGGER_ANIMATION = 3;

@Component({
    selector: "sf-badge",
    templateUrl: "./badge.component.html"
})

export class SfBadgeComponent implements OnInit {
    @Input() look: string;
    @Input() iconLook: string;
    @Input() iconName: string;
    @Input() content: string;
    @Input() color: string;
    isAnimated = false;
    hasIcon = false;
    badgeCssClassList: string;

    @ViewChild("contentWrapper", { static: true }) contentWrapper;

    ngOnInit() {
        this.hasIcon = !!(this.iconLook || this.iconName);
        this.isAnimated = this.content && (this.content.length > SYMBOLS_COUNT_TO_TRIGGER_ANIMATION);
        this.badgeCssClassList = this.generateCssClassList();
    }

    generateCssClassList() {
        let cls = iconClass;

        if (this.look) {
            cls += " -" + this.look.split(" ").join(" -");
        }
        if (this.hasIcon) {
            cls += " -icon";
        }
        if (this.isAnimated) {
            cls += " -floating-txt";
        }
        return cls;
    }
}
