import { Component, Input, Output, EventEmitter, OnInit, SimpleChanges, OnChanges } from "@angular/core";

const notificationClass = "sf-notification";
const tooltipModifier = "tooltip";
const fixedModifier = "fixed";
const inlineModifier = "inline";

@Component({
    selector: "sf-notification",
    templateUrl: "./notification.component.html"
})
export class SfNotificationComponent implements OnInit, OnChanges {
    @Input() message: string;
    @Input() look: string;
    @Input() caption: string;
    @Output() onClose = new EventEmitter();
    public isTooltip: boolean = false;
    public isFixed: boolean = false;

    notificationCssClassList: string;

    ngOnInit() {
        this.isTooltip = this.look && this.look.includes(tooltipModifier);
        this.isFixed = this.look && this.look.includes(fixedModifier);
        this.notificationCssClassList = this.generateCssClassList();
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes) {
            this.handleChanges(changes);
        }
    }

    generateCssClassList(): string {
        let cls = notificationClass;

        if (this.look) {
            cls += " -" + this.look.split(" ").join(" -");

            if (this.isFixed && !this.look.includes(inlineModifier)) {
                cls += " -" + inlineModifier;
            }
        }

        return cls;
    }

    checkObservers() {
        return this.onClose.observers.length;
    }

    onCloseButtonClick(): void {
        this.onClose.emit();
    }

    private handleChanges(changes: SimpleChanges) {
        if (changes.look && changes.look.previousValue !== changes.look.currentValue) {
            this.notificationCssClassList = this.generateCssClassList();
        }

        if (changes.caption && changes.caption.previousValue !== changes.caption.currentValue) {
            this.caption = changes.caption.currentValue;
        }
    }
}

// TODO: change to ENUM when TypeScript 2.4+ is included
export class NotificationPosition {
    static Up = "up";
    static Down = "down";
    static Left = "left";
    static Right = "right";
}
