import {
    Component, Input, Output, EventEmitter, OnInit, ViewEncapsulation, HostBinding,
    ElementRef, AfterViewInit, ChangeDetectorRef, OnChanges
} from "@angular/core";
import { coerceBoolean } from "../utils";

const notificationClass = "sf-notification -tooltip";
const faq = "faq";
const gotIt = "Got it";

@Component({
    selector: "sf-tooltip",
    templateUrl: "./tooltip.component.html",
    styleUrls: ["./tooltip.component.scss"],
    encapsulation: ViewEncapsulation.None
})
export class TooltipComponent implements OnInit, AfterViewInit, OnChanges {
    @Input() content: string;
    @Input() look: string;

    showContent: boolean;
    cssClassList: string;
    iconLook: string;
    tooltipDimensions: ElementDimensions;

    @Output() close = new EventEmitter();
    @Output() skipAll = new EventEmitter();

    @Input() set appendToParent(appendToParent: boolean) {
        this._appendToParent = coerceBoolean(appendToParent);
    }
    get appendToParent(): boolean {
        return this._appendToParent;
    }

    @Input() set seen(seen: boolean) {
        this._seen = coerceBoolean(seen);
    }
    get seen(): boolean {
        return this._seen;
    }

    @Input() set showOnce(showOnce: boolean) {
        this._showOnce = coerceBoolean(showOnce);
    }
    get showOnce(): boolean {
        return this._showOnce;
    }

    @Input() set fixedDimensions(fixedDimensions) {
        if (!fixedDimensions) {
            return;
        }

        let dimensions;
        try {
            dimensions = JSON.parse(fixedDimensions);
        } catch (e) {
            dimensions = fixedDimensions;
        }
        this._fixedDimensions = {
            top: dimensions.top,
            right: dimensions.right,
            bottom: dimensions.bottom,
            left: dimensions.left,
            position: dimensions.position
        };
    }
    @Input() set targetDimensions(targetDimensions) {
        let dimensions;
        try {
            dimensions = JSON.parse(targetDimensions);
        } catch (e) {
            dimensions = targetDimensions;
        }
        this._targetDimensions = {
            top: Math.floor(Number(dimensions.top)),
            right: Math.floor(Number(dimensions.right)),
            bottom: Math.floor(Number(dimensions.bottom)),
            left: Math.floor(Number(dimensions.left)),
            height: Math.floor(Number(dimensions.height)),
            width: Math.floor(Number(dimensions.width))
        };
    }
    @Input() set boxModelDimensions(boxModelDimensions) {
        this._boxModelDimensions = boxModelDimensions;
    }

    @Input() gotItLinkText: string = gotIt;
    @Input() zIndex: number;
    @Input() isLastElement: boolean = false;

    @HostBinding("style.zIndex") zIndexValue: number = 99999;
    @HostBinding("style.transform") transform: string;
    @HostBinding("class.-sf-invisible") isHidden: boolean = true;

    private _appendToParent: boolean = false;
    private _seen: boolean = false;
    private _showOnce: boolean = false;
    private _fixedDimensions: Dimensions;
    private _targetDimensions: ElementDimensions;
    private _boxModelDimensions: BoxModelDimensions;
    private _isInitialized: boolean;

    constructor(private element: ElementRef, private ref: ChangeDetectorRef) {}

    ngOnChanges() {
        if (this._isInitialized) {
            this.initializeTooltip();
            this.positionTooltip();
        }
    }

    ngOnInit() {
        this.initializeTooltip();
        this._isInitialized = true;
    }

    initializeTooltip() {
        this.showContent = this.showOnce && !this.seen;
        this.cssClassList = this.generateCssClassList();

        if (this.look && this.look.includes(faq)) {
            this.zIndexValue = 99998;
            this.iconLook = "size-2xl light faq";
        } else {
            this.iconLook = "size-xs";
        }

        if (this._fixedDimensions) {
            this.setDimensions(this._fixedDimensions);

            if (this._fixedDimensions["position"]) {
                this.element.nativeElement.style.position = this._fixedDimensions["position"];
            } else {
                this.element.nativeElement.style.position = "fixed";
            }
        } else {
            this.element.nativeElement.style.position = "absolute";
        }

        if (this.zIndex) {
            this.zIndexValue = this.zIndex;
        }
    }

    positionTooltip() {
        const boundingRect = this.element.nativeElement.getBoundingClientRect();
        this.tooltipDimensions =  {
            top: Math.floor(boundingRect.top),
            right: Math.floor(boundingRect.right),
            bottom: Math.floor(boundingRect.bottom),
            left: Math.floor(boundingRect.left),
            height: Math.floor(boundingRect.height),
            width: Math.floor(boundingRect.width)
        };

        if (this._fixedDimensions || this.appendToParent || (this.look && this.look.includes(faq))) {
            this.isHidden = false;

            if (this.appendToParent) {
                this.element.nativeElement.style.display = "inline-block";
            }
        } else {
            this.calculatePosition();
        }

        if (this._boxModelDimensions) {
            this.setDimensions(this._boxModelDimensions);
        }
    }

    ngAfterViewInit() {
        this.positionTooltip();
    }

    toggleContent(event?: any) {
        this.showContent = !this.showContent;
        this.ref.detectChanges();
        if (event) {
            event.stopPropagation();
        }
    }

    onTooltipClick(event: any) {
        event.stopPropagation();
    }

    calculatePosition() {
        let left = 0;
        let top = 0;

        if (this.look) {
            if (this.look.startsWith("up")) {
                top += this._targetDimensions.top - this.tooltipDimensions.bottom;
            }

            if (this.look.startsWith("down")) {
                top += -this.tooltipDimensions.top + this._targetDimensions.bottom;
            }

            if (this.look.startsWith("left")) {
                left += this._targetDimensions.left - this.tooltipDimensions.right;
            }

            if (this.look.startsWith("right")) {
                left += this._targetDimensions.right - this.tooltipDimensions.left;
            }

            if (this.look.includes("-right")) {
                left += this._targetDimensions.right - this.tooltipDimensions.right;
            }

            if (this.look.includes("-left")) {
                left += this._targetDimensions.left - this.tooltipDimensions.left;
            }

            if (this.look.includes("-up")) {
                top += this._targetDimensions.top - this.tooltipDimensions.top;
            }

            if (this.look.includes("-down")) {
                top += this._targetDimensions.bottom - this.tooltipDimensions.top - this.tooltipDimensions.height;
            }

            if (this.look === "right" || this.look === "left" ) {
                top += this._targetDimensions.bottom  - this.tooltipDimensions.top - (this.tooltipDimensions.height + this._targetDimensions.height) / 2;
            }
            if (this.look === "up" || this.look === "down" ) {
                left += this._targetDimensions.left  - this.tooltipDimensions.left + (this._targetDimensions.width - this.tooltipDimensions.width) / 2;
            }

            this.transform = "translate3d(" + left + "px, " + top + "px, 0)";
        }

        this.isHidden = false;
    }

    setDimensions(dimensionsObj: Object): void {
        for (const key of Object.keys(dimensionsObj)) {
            if (dimensionsObj[key] !== undefined && dimensionsObj[key] !== "position") {
                this.element.nativeElement.style[key] = dimensionsObj[key] + "px";
            }
        }
    }

    triggerClose(event: any) {
        this.toggleContent();
        this.seen = true;
        this.close.emit();
        this.ref.detectChanges();
        event.stopPropagation();
    }

    triggerSkipAll(event: any) {
        this.skipAll.emit();
        this.triggerClose(event);
    }

    private generateCssClassList(): string {
        let cls = notificationClass;

        if (this.look && !this.look.includes(faq)) {
            cls += " -help";
        }

        if (this.look) {
            cls += " -" + this.look.split(" ").join(" -");
        }

        return cls;
    }
}

export interface ElementDimensions extends Size, Dimensions {
}

export interface Dimensions {
    top?: number;
    right?: number;
    bottom?: number;
    left?: number;
    position?: string;
}

export interface BoxModelDimensions extends Size {
    paddingTop?: number;
    paddingRight?: number;
    paddingBottom?: number;
    paddingLeft?: number;
    marginTop?: number;
    marginRight?: number;
    marginBottom?: number;
    marginLeft?: number;
}

export interface Size {
    height?: number;
    width?: number;

}
