import { Component, Output, EventEmitter, Input } from "@angular/core";

@Component({
    selector: "sf-switch",
    templateUrl: "./switch.component.html"
})
export class SfSwitchComponent {
    @Input() id: string;
    @Input() onText: string;
    @Input() offText: string;
    @Input() isOn: boolean = false;
    @Input() showText: boolean = true;
    @Input() isLoading: boolean = false;
    @Input() disabled: boolean = false;

    @Output() selectionChange: EventEmitter<void> = new EventEmitter<void>();

    onSelectionChanged(event: Event): void {
        if (event) {
            event.preventDefault();
        }

        this.selectionChange.emit();
    }

    getOnOrOffText(): string {
        return this.isOn ? this.onText : this.offText;
    }

    getOnOrOffButtonIconName(): string {
        return this.isOn ? "toggle-on" : "toggle-off";
    }

    getOnOrOffButtonLook(): string {
        let iconLook = "";
        if (this.isOn) {
            iconLook = "positive";
        } else if (this.disabled) {
            iconLook = "lightest";
        }
        return iconLook + " size-xl sf-ml-3xs";
    }
}
