import { InjectionToken, FactoryProvider } from "@angular/core";

// TODO: #227311 move to shared
export const WINDOW_TOKEN = new InjectionToken("Window");

export function getWindowFactory() {
    return (typeof window !== "undefined") ? window : null;
}
// http://stackoverflow.com/a/37176929
export const WINDOW_PROVIDER: FactoryProvider = {
    provide: WINDOW_TOKEN,
    useFactory: getWindowFactory
};
